import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const AddForm = (props) => {
    return (
        <form className="client-form" 
            onSubmit={props.handleSubmit}>
            <FontAwesomeIcon 
                className="close-btn"
                icon="times-circle" 
                size="lg"
                title="close form"
                onClick={props.closeAddForm}
            />
            <h2>Add Clients</h2> 
            <input name="name" type="text" placeholder="name" value={props.clientName} onChange={props.handleChange}/>
            <input name="age" type="text" placeholder="age" value={props.clientAge} onChange={props.handleChange}/>
            <input name="id" type="text" placeholder="id" value={props.clientId} onChange={props.handleChange}/>
            <input type="submit" placeholder="submit"/>
        </form>
    )

}

export default AddForm
