import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const AdminHeader = (props) => {

    return (
        <div className="main-header">
            <div className="main-wrapper header-container">
                <p>Client Status</p>
                <div className="header-btn-wrapper">
                    <div onClick={props.showAddForm}>
                    <FontAwesomeIcon 
                        icon="plus-circle" 
                        size="2x"
                        className="add-btn"
                    />
                    <p>
                        Add Client
                    </p>
                    </div>
                    <div>
                    <FontAwesomeIcon 
                        icon="info-circle" 
                        size="2x"
                    />
                    <p>
                        Help
                    </p>
                    </div>
                    <div className="logout-btn" 
                        title="logout" 
                        onClick={props.loggedOut}
                    >
                        <FontAwesomeIcon 
                            icon="user-circle" 
                            size="2x"
                        />
                        <p>
                            Logout
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AdminHeader