import React, { useEffect, useState } from 'react';
// import useForm from 'react-hook-form';
import useForm from './customFunction/useForm';
import { Link, Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
import { faOm } from '@fortawesome/free-solid-svg-icons';

const Login = (props) => {
    const {user, handleLoginChange, handleSubmit} = useForm(submit)
    // create state here
    // create form and compare state values to default values

    // console.log(user.name)
    // console.log(user.password)

    function submit () {
        loginAdmin()
    }

    const admin = {
        name: "admin",
        password: "123456",
    }

    const loginAdmin = () => {
        // props.login();
        const loginUser = {
            name: user.name,
            password: user.password
        }

        if(loginUser.name === admin.name && loginUser.password === admin.password) {
            console.log("WELCOME admin")
            props.login();
        }
    }    

    if(props.isLogin === "true"){
        console.log("you're login")
        return(
            <Redirect to="/admin" />
        )
    }

    return (
        <div className="admin-login-container">
            <h1>Admin Login</h1>
            <div className="form-container">
                <form className="login-form" onSubmit={handleSubmit}>
                    <input type="text" placeholder="name" name="name" value={user.name} onChange={handleLoginChange} />
                    <input type="password" placeholder="password" name="password" value={user.password} onChange={handleLoginChange} />
                    <button type="submit">login</button>
                </form>
                <Link to="/" className="login-home-btn">Go to Home Page</Link>
            </div>
        </div>
    )
}

export default Login;