import React, { useEffect, useState, useRef } from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Switch, Redirect, withRouter, Link, NavLink } from 'react-router-dom';
import Home from './components/Home';
import Login from './components/Login';
import Admin from './components/Admin';
import { library } from '@fortawesome/fontawesome-svg-core';
import Cookies from 'universal-cookie';
import { faUserCircle, faInfoCircle, faTimesCircle, faMinusCircle, faEdit, faTrash, faPlusCircle } from '@fortawesome/free-solid-svg-icons';

library.add(faUserCircle, faInfoCircle, faTimesCircle, faMinusCircle, faEdit, faTrash, faPlusCircle );

const usePrevious = (value) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};
// add conment
const App = () => {

  const [login, setLogin] = useState(false);

  const cookies = new Cookies();

  // check updates of login, refer to movie info app
  const prevLogin = usePrevious(login);
  const isLog = cookies.get('isLogin'); 

  useEffect(() => {
    console.log("welcome");
  },[])

  // useEffect(() => {
  //   // cookies.set('isLogin', login, { path: '/', expires: 0 });
  //   if(prevLogin !== "true"){
  //     console.log("prevLog")
  //     setLogin(true);
  //   } else {
  //     setLogin(false);
  //   }
  // },[isLog])
  
  const authenticate2 = () => {
    cookies.set('isLogin', true, { path: '/', expires: 0 });
    setLogin(isLog);
  }

  const logout = () => {
    cookies.set('isLogin', false, { path: '/', expires: 0 });
    setLogin(isLog);
  }

  console.log(isLog)


  const SecuredRoute = (props) => {
    return (
      <Route path={props.path} render={ data => isLog === "true" ? 
        ( <props.component {...data}></props.component> ) :
        (<Redirect to={{pathname: '/login'}}></Redirect>) }>
      </Route>
    )
  }

  return (
    <div className="App">
      <Router>
        <Switch>	
          <Route 
            exact path="/" 
            component={ Home } 
          />
          <Route
            exact path="/login" 
            component={ () => <Login login={authenticate2} isLogin={isLog} /> } 
          />
          <SecuredRoute 
            exact path="/admin" 
            component={ () =>  <Admin logout={logout} />} 
          />
        </Switch>
      </Router>
    </div>
  );
}

export default App;