import { useState } from 'react';

const useForm = (callbackAdd, callbackEditSubmit, callbackUserSubmit) => {
    const [client, setClient] = useState({ name: '', age: '', id: '' });
    const [editClient, setEditClient] = useState({ name: "", age: "", id: "" });
    const [user, setUser] = useState({ name: "", password: "" });

    // HANDLE ADD CLIENT
    const handleChange = (e) => {
        // console.log(e.target.value);
        const { name, value } = e.target;

        setClient({
            ...client,
            [name]: value
        })
    } 

    const handleSubmit = (e) => {
        e.preventDefault();
        callbackAdd();
    };


    // HANDLE EDIT CLIENT
    const handleEditChange = (e) => {
        // console.log(e.target.value);
        const { name, value } = e.target;

        setEditClient({
            ...editClient,
            [name]: value
        })
    }   

    const handleGetEdit = (client) => {
        setEditClient({
            name: client.name,
            age: client.age,
            id: client.id
        })
    }

    const handleEditSubmit = (e) => {
        e.preventDefault();
        callbackEditSubmit();
    }

    // handle login
    const handleLoginChange = (e) => {
        // console.log(e.target.value);
        const { name, value } = e.target;

        setUser({
            ...user,
            [name]: value
        })

        console.log(user.name)
        console.log(user.password)
    } 

    return {
        handleChange,
        handleSubmit,
        client,
        handleEditChange,
        handleGetEdit,
        handleEditSubmit,
        editClient,
        handleLoginChange,
        user
    }
};

export default useForm;