import React from 'react';
import { useEffect, useState, useRef } from 'react';
import useForm from './customFunction/useForm';
import clientData from './data/clients.json';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import axios from 'axios';
import AdminHeader from './AdminHeader';
import AddForm from './AddForm';
import UpdateForm from './UpdateForm';

const usePrevious = (value) => {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
};

const Admin = (props) => {

    const [clientList, setClientList] = useState([]);

    const url =  `${window.location.protocol}//${window.location.hostname}`;

    const prevParams = usePrevious(props.length);

    const fetchData = async () => {
        const clientApi = `${url}/clients/data`,
            result = await axios.get(clientApi);
        setClientList(result.data);
    }

    useEffect(() => {
        fetchData();
    },[]);

    useEffect(() => {
        if ( prevParams != clientList.length )
         fetchData();
    },[]);

    // custom hook
    // -- please refer on ./customFunction/useForm.js
    const {handleChange, handleSubmit, client, handleEditChange, handleGetEdit, handleEditSubmit, editClient} = useForm(addClients, updateClient);
    // end of custom hook

    const [addForm, setAddForm] = useState(false);
    const [editForm, setEditForm] = useState(false);

    console.log(`current url: ${url}`)

    // buttons
    const loggedOut = () => { props.logout() };
    const showEditForm = () => { setEditForm(true) };
    const closeEditForm = () => { setEditForm(false) };
    const showAddForm = () => { setAddForm(true) };
    const closeAddForm = () => { setAddForm(false) };
    // end of buttons

    // AXIOS REQUEST
    function addClients () {
        closeAddForm();
        const newClient = {
            name: client.name,
            age: client.age,
            id: client.id
        }
        axios.post(`${url}/clients/data`, newClient)
            .then(res => console.log(res.data));
    }

    function updateClient () {
        closeEditForm();
        axios.put(`${url}/clients/data/${editClient.id}`, editClient)
            .then(res => console.log(res.data))
            .catch(err => console.log(err));
    }

    const deleteClient = (clientId) => {
        axios.delete(`${url}/clients/data/${clientId}`)
            .then(res => console.log(res.data))
            .catch(err => console.log(err));
    }
    // END OF AXIOS REQUEST

    const noClient = () =>  <h2>NO CLIENT</h2>;

    // create client content
    const clientDetails = clientList.map((client) => {
        const handleDelete = (e) => {
            e.preventDefault();
            // console.log(`client id: ${client.id}`)
            deleteClient(client.id);
        }
        const getClientEdit = (e) => {
            e.preventDefault();
            showEditForm();
            handleGetEdit(client)
        }

        return (
            <div className="client-box" key={client.id}>
                <div className="client-btn-wrapper">
                    <FontAwesomeIcon 
                        className="client-btn"
                        icon="edit" 
                        size="xs"
                        title="update client"
                        onClick={getClientEdit}
                    />
                    <FontAwesomeIcon 
                        className="client-btn"
                        icon="trash" 
                        size="xs"
                        title="delete client"
                        onClick={handleDelete}
                    />
                </div>
                <p>{client.name}</p>
                <p>{client.age}</p>
            </div>
        )
    });
    // end of create client content

    return (

        <div className="admin-container">
            <div className="sidebar">
                 {/* create seperate component - AdminHeader */}
            </div>
            <div className="main-container2">

                <AdminHeader
                    showAddForm={showAddForm}
                    loggedOut={loggedOut}
                />
               
                {/* end of header */}

                <h2>Hello Admin</h2>
                <div className="main-wrapper">
                    <div className="client-container">
                        { clientList.length > 0 ? clientDetails : noClient() }
                    </div>
                </div>
            </div>
            {/* FORMS */}
            <div className={addForm ? "client-form-container" : "hide-form"}>
                <AddForm
                    handleSubmit={handleSubmit}
                    handleChange={handleChange}
                    closeAddForm={closeAddForm}
                    clientName={client.name}
                    clientAge={client.age}
                    clientId={client.id}
                />
            </div>
            <div className={editForm ? "client-form-container" : "hide-form"}>
                <UpdateForm
                    handleUpdateSubmit={handleEditSubmit}
                    handleUpdateChange={handleEditChange}
                    closeUpdateForm={closeEditForm}
                    clientName={editClient.name}
                    clientAge={editClient.age}
                />
            </div>
            {/* FORMS END */}
        </div>
    ) 
    
}

export default Admin