import React, { useEffect, useState } from 'react';
import axios from 'axios';
import clientData from './data/clients.json';
import { Link } from 'react-router-dom';

const Home = () => {
    const [clientList, setClientList] = useState([]);

    const url =  `${window.location.protocol}//${window.location.hostname}`;

    useEffect(() => {
        const fetchData = async () => {
            const clientApi = `${url}/clients/data`,
                result = await axios.get(clientApi);
            setClientList(result.data);
        }

        fetchData();
     },[]);
     
    const noClient = () =>  <h2>NO CLIENT</h2>;
    const showClients = clientList.map((client) => {
        return (
            <div className="client-box">
                <p>{client.name}</p>
                <p>{client.age}</p>
            </div>
        )
    });

    return (
        <div>
            <div className="main-header">
                <div className="main-wrapper header-container">
                    <p>Client Status</p>
                    <Link to="/login" className="login-btn">Login</Link>
                </div>
            </div>      
            <div className="main-wrapper">
                <div className="client-container">
                    { clientList.length > 0 ? showClients : noClient() }
                </div>
            </div>
        </div>
    ) 

}

export default Home;