import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const UpdateForm = (props) => {
    return (
        <form className="client-form" 
        onSubmit={props.handleUpdateSubmit}>
        <FontAwesomeIcon 
            className="close-btn"
            icon="times-circle" 
            size="lg"
            title="close form"
            onClick={props.closeUpdateForm}
        />
        <h2>Update Form</h2>
        <input name="name" type="text" placeholder="name" value={props.clientName} onChange={props.handleUpdateChange}/>
        <input name="age" type="text" placeholder="age" value={props.clientAge} onChange={props.handleUpdateChange}/>
        <input type="submit" placeholder="submit"/>
    </form>
    )
}

export default UpdateForm
